* {
    box-sizing: border-box;
}

.container {
    perspective: 1000px;
    text-align: center;
}

.card {
    position: relative;
    width: 460px;
    height: 318px;
    color: #ffffff;
    text-align: left;
    font-family: NotoSans,
    'Trebuchet MS';
    transform-style: preserve-3d;
    h1 {
        height: 52px;
        font-size: 24px;
        font-weight: 700;
        border-bottom: 1px solid white;
        vertical-align: bottom;
        margin-left: 31px;
        padding-bottom: 10px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
    input {
        color: rgb(27.7%, 27.7%, 27.7%);
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        &::placeholder {
            color: rgba(black, 0.4);
        }
        &:-webkit-autofill {
            background-color: white !important;
            -webkit-box-shadow: 0 0 0px 1000px white inset;
            -webkit-text-fill-color: black !important;
        }
    }

    input[type=number]::-webkit-inner-spin-button,
    input[type=number]::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }
}


.btn__turnOver {
    position: absolute;
    color: rgba(white, 0.7);
    right: 10px;
    top: 10px;
    padding: 0.5rem;
    border-radius: 50%;
    cursor: pointer;
}

.card__front,
.card__back {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    z-index: 2;
    h3 {
        font-size: 12px;
        font-weight: 400;
        color: white;
        width: 100%;
        padding-bottom: 12px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
}


.card__front {
    position: relative;
    background-image: url('./images/bg_ic.svg'), url('./images/bg_visa.png'), linear-gradient(0deg, #003333 0%, #336699 100%);
    background-repeat: no-repeat;
    background-size: 50px 50px, 230px 79px, 100% auto;
    background-position: right 31px bottom 46px, right 0px bottom 17px, left top;
    transform: rotateY(0deg);
    z-index: 2;
}

.card__back {
    padding: 118px 0 0 31px;
    background-image: linear-gradient(transparent 52px, black 52px, black 94px, transparent 94px), url('./images/bg_visa.png'), linear-gradient(0deg, #003333 0%, #336699 100%);
    background-repeat: no-repeat;
    background-size: 100% 100%, 230px 79px, 100% auto;
    background-position: left top, right 0px bottom 17px, left top;
    transform: rotateY(180deg);
    z-index: -1;
}

.front__inputs {
    padding: 0 31px;
}

.block__munDraC {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    h3 {
        height: 86px;
    }
}

.label__expiration {
    display: block;
    margin-top: 35px;
    input {
        margin-right: 15px;
    }
}

.input__style {
    display: inline-block;
    box-sizing: border-box;
    background-color: #ffffff;
    width: 89px;
    height: 27px;
    border-radius: 2px;
    box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.6), 0 0 0px 1000px white inset;
    text-align: center;
    font-size: 20px;
    border: none;
    border-radius: 3px;
}

.input__invalid {
    border: 2px solid rgba(red, 0.8);
    &:focus {
        outline: rgba(red, 0.8);
    }
}

.label__cardCVC {
    h3 {
        height: 53px;
    }
}

.input__card_holder_name {
    width: 370px;
}

.input__cardCVC {
    width: 130px;
}

.btn__submit {
    margin-top: 41px;
    border-radius: 9px;
    background: linear-gradient(0deg, #003333 0%, #336699 100%);
    box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.35);
    width: 192px;
    height: 66px;
    display: inline-block;
    font-family: 'Trebuchet MS';
    color: #ffffff;
    font-size: 26.4px;
    font-weight: 400;
    line-height: 66px;
    text-align: center;
    filter: grayscale(100%);
    cursor: not-allowed;
    transition: 0.2s linear; // cursor: 
     :global &--active {
        filter: grayscale(0);
        cursor: pointer;
    }
}



